  /* ----------popup---------- */
  .popup_wrap {
    display: none;
    width: 860px;
    height: 500px;
    background-color: rgb(255, 255, 255);
  }
  .popup_wrap .popup_inner {
    height: 100%;
    position: relative;
    display: flex;
  }
  .popup_wrap .popup_inner .popup_img {
    width: 387px;
    height: 100%;
    border: 1px solid #777;
    box-sizing: border-box;
    padding: 30px 0px;
  }
  .popup_wrap .popup_inner .popup_img img {
    width: 100%;
  }
  .popup_wrap .popup_inner .txt_wrap {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 473px;
    height: 100%;
    font-family: "Pretendard-Regular";
  }
  .popup_wrap .popup_inner .txt_wrap li {
    cursor: default;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_eng {
    margin-top: 95px;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_kor {
    margin-top: 15px;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_gift {
    margin-top: 50px;
    font-size: 14px;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_scent {
    font-size: 14px;
    text-align: center;
    padding: 0px 35px;
    margin-top: 15px;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_price {
    margin-top: 45px;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_btn {
    width: 290px;
    height: 35px;
    line-height: 35px;
    background-color: #222;
    color: #fff;
    text-align: center;
    border-radius: 20px;
    margin-top: 60px;
    transition: all 0.3s;
    cursor: pointer;
  }
  .popup_wrap .popup_inner .txt_wrap .txt_btn:hover {
    color: #000;
    background-color: #fff;
    border: 1px solid #222;
    box-sizing: border-box;
  }
  .popup_wrap .popup_inner .pop_btn {
    position: absolute;
    top: 25px;
    right: 25px;
    cursor: pointer;
  }
